.ButtonModal {
  width: 20%;
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
}

.WideButton {
  background-color: #00904A;
  color: #FDFDFD;
  padding: 5px 0;
  outline: none;
  border: none;
  border-radius: 0.3em;
  width: 90%;
}

.calendar_container {
  width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  text-align: center;
}
.calendar_container .headingtext {
  margin-bottom: 20px;
}
.calendar_container .headingtext .headingtext h1 {
  font-size: 30px;
}
.calendar_container .type {
  display: flex;
  justify-content: space-evenly;
  width: 100%;
  margin-bottom: 20px;
}
.calendar_container .type button {
  width: 50px;
  height: 15px;
  border: none;
  border-radius: 15px;
}
.calendar_container .type .leftred {
  display: flex;
  font-size: 15px;
}
.calendar_container .type .leftred .red {
  display: flex;
  justify-content: flex-start;
  background: red;
}
.calendar_container .type .rightgreen {
  display: flex;
  font-size: 15px;
}
.calendar_container .type .rightgreen .green {
  display: flex;
  justify-content: flex-end;
  background: #00904A;
}
.calendar_container .calendar {
  width: 100%;
  height: auto;
  box-shadow: 0 0.5rem 3rem rgba(0, 0, 0, 0.4);
}

.create {
  font-size: 18px;
}

.create a {
  text-decoration: none;
  color: #000;
}

.month {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 2rem;
  text-align: center;
  text-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.5);
  background: lightgray;
}

.prev-date,
.next-date {
  opacity: 0.2;
}

.weekdays {
  width: 100%;
  height: 50px;
  padding: 0 5px;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.weekdays div {
  font-size: 16px;
  font-weight: 400;
  letter-spacing: 0.1rem;
  width: 5.7142857143rem;
  display: flex;
  justify-content: center;
  align-items: center;
  text-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, 0.5);
  font-weight: bold;
}

.days {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  padding: 0 10px;
  font-size: 14px;
}

.days div {
  width: 40px;
  margin: 0px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: background-color 0.1s;
}

.days div:hover:not(.today) {
  background-color: #005140;
  border-radius: 30px;
  cursor: pointer;
}

.today {
  background-color: #00904A;
  border-radius: 30px;
}

.month i {
  font-size: 20px;
  cursor: pointer;
  color: #000;
}

.month .date p {
  font-size: 14px;
  font-weight: lighter;
}

/*Responsive table Driver*/
.table_container {
  width: 100%;
  margin: 0 auto;
  height: auto;
}

table {
  width: 100%;
  border-collapse: collapse;
  color: #f3f3f3;
}

td, th {
  padding: 8px;
  text-align: left;
}

/* Zebra striping */
tr:nth-of-type(odd) {
  background: red;
}

/* Zebra striping */
tr:nth-of-type(even) {
  background: #00904A;
}

thead {
  height: 50px;
}
thead th {
  background: #EAEAEA;
  font-weight: bold;
  color: #000000;
}

tbody tr {
  overflow-y: auto;
}
tbody a {
  text-decoration: none;
  color: #fff;
}
tbody td a:nth-child(2) {
  color: #000;
}
tbody td a {
  text-decoration: none;
  color: white;
}

tbody tr:last-child {
  background-color: lightgray;
}

/*CRUD STYLING*/
form {
  background: #fff;
  border: none;
  transition: all 0.3s ease;
}

.form-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

label {
  font-size: 25px;
  color: #000;
}

input[class=form-control] {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 15px;
  box-sizing: border-box;
}

input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  border: 1px solid #ccc;
  border-radius: 15px;
  box-sizing: border-box;
}

input[type=submit] {
  width: 100%;
  background-color: #00904A;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 15px;
  cursor: pointer;
}

button:hover {
  background: #005140;
}

.back_list {
  margin-top: 20px;
  text-decoration: none;
}

.back_list i {
  color: #000;
  text-decoration: none;
  font-style: normal;
}

a {
  text-decoration: none;
}

a:hover {
  color: #DC2F34;
}